import { SingletonApolloClient } from '../singleton/client';

export const readDataGQL = async(variables, query)=>{
    const client = new SingletonApolloClient().getClient()
    try{
        console.log({
            variables: variables,
            query: query
        })
        let res = await client
            .query({
                variables: variables,
                query: query
            })
        console.log(res.data)
        /*client.writeQuery({
            variables: variables,
            query: query,
            data: res.data,
        });*/
        return res.data
    } catch(err){
        let res = client.readQuery({
            variables: variables,
            query: query,
        });
        console.log(res)
    }
}